        { title: 'Cost', path: '/cost', icon: 'mdi-wallet', text: 'Ïó¨Ìñâ ÎπÑÏö©', isActive: false },
        { title: 'Favorits', path: '/favorits', icon: 'mdi-star-box', text: 'Ïó¨Ìñâ Ï¶êÏ∞æ', isActive: false },
        { title: 'Share', path: '/share', icon: 'mdi-share-variant', text: 'Ïó¨Ìñâ Ï∂îÏ≤ú', isActive: false },
