<template>


  <div class="chat-room">
    <div>
      <div class="chat-toggle" v-if="!showChat" @click="enterChat($event)">{{ chatRoomText }}</div>  <!-- Ï±ÑÌåÖÎ∞©ÏûÖÏû• -->
    </div>
    <div class="chat-toggle"  v-if="showChatter" @click="toggleChatList($event)">
      Ï±ÑÌåÖÏûê
    </div>
    <div v-if="showChatterList" class="chat-list-window">
      <ul>
        <li v-for="chatter in chatterTotalList" :key="chatter.userNo">
          <span class="emoticon">üòä</span>
          <span class="user-name">{{chatter.userName}}</span>
          <span class="date"> ( {{chatter.date}} )</span>
        </li>
      </ul>
    </div>
    <div class="chat-toggle"  v-if="showChat" @click="toggleChat($event)">                           <!-- Ï±ÑÌåÖÎ∞© -->
      {{ chatRoomText }}
       <button class="exit-button" v-if="showChat" @click="exitChat($event)">ÎÇòÍ∞ÄÍ∏∞</button>
    </div>
   
    <div v-if="showChat" class="chat-window">
      <!-- Ï±ÑÌåÖ ÎÇ¥Ïö© ÌëúÏãú -->
      <ul class="chat-container">
         <!-- Ïù¥Ï†Ñ Î©îÏãúÏßÄ -->
        <li v-for="previousChat in previousChatMessages" :key="previousChat.date" 
          :class="{ 'user-chat': this.userNo === previousChat.userNo, 
                    'other-chat': this.userNo !== previousChat.userNo, 
                    'system-chat': previousChat.systemMessage,
                  }"
          class="chat">
          <div>
            <v-container>
            <v-row>
              <v-col>
              <!-- <span v-if="this.userNo === previousChat.userNo" class="other-person-name">{{ previousChat.userName }}</span> -->
              <span v-if="!previousChat.systemMessage" class="other-person-name">{{ previousChat.userName }}</span>
              <span v-if="!previousChat.systemMessage" class="chat-date">  [{{ previousChat.date }}]  </span>
              <div class="message-content">
                {{ previousChat.text }}
              </div>
              </v-col>
            </v-row>
            </v-container>
          </div>
        </li>
        <li v-for="chat in chatTotalMessages" :key="chat.date" 
          :class="{ 'user-chat': this.userNo === chat.userNo, 
                    'other-chat': this.userNo !== chat.userNo, 
                    'system-chat': chat.systemMessage,
                  }"
          class="chat"
        >
        <div class="scroll-to-bottom" v-if="!isScrolledToBottom" @click="scrollToBottom">
           ‚è¨ÏµúÏã† Î©îÏÑ∏ÏßÄ Ïù¥Îèô‚è¨
        </div>
        <div>
          <v-container>
            <v-row>
              <v-col>
              <!-- <span v-if="this.userNo === chat.userNo" class="other-person-name">{{ chat.userName }}</span> -->
              <span v-if="!chat.systemMessage" class="other-person-name">{{ chat.userName }}</span>
              <span v-if="!chat.systemMessage" class="chat-date">  [{{ chat.date }}]  </span>
              <div class="message-content">
                {{ chat.text }}
              </div>
              </v-col>
            </v-row>
          </v-container>
        </div>
        </li>
      </ul>
    </div>
    <div v-if="showChat">
        <!-- Ï±ÑÌåÖ ÏûÖÎ†• Ìèº -->
      <div class="input-container">
        <input type="text" v-model="newMessage" @keyup.enter="sendMessage" placeholder="Î©îÏãúÏßÄÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.">
        <button @click="sendMessage">Ï†ÑÏÜ°</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  mounted(){
    window.addEventListener('wheel', this.handleScroll);
  },
  created(){
    this.userNo = sessionStorage.getItem("userNoSession");
    // this.userNo = '66f21fb2b35c'; // ÏûÑÏãú
    this.projectNo = sessionStorage.getItem("projectNoSession");
    this.getTripUserInfo();
    
  },
  data() {
    return {
      showChat: false,
      showChatterList: false,
      showChatter: false,
      chatRoomText: 'Ï±ÑÌåÖÎ∞© ÏûÖÏû•',
      chatMessages: [], // Ï±ÑÌåÖ Î©îÏÑ∏ÏßÄ (send Ï†ÑÎã¨ Ïö©)
      chatTotalMessages: [], // Ï±ÑÌåÖ Ï†ÑÏ≤¥ ÎÇ¥Ïó≠
      previousChatMessages: [], // Ïù¥Ï†Ñ ÎÇ¥Ïó≠ Î∂àÎü¨ÏôÄÏÑú Î≥¥Ïó¨Ï£ºÍ∏∞
      // chatterList: [],  // Ï±ÑÌåÖÏûê Î¶¨Ïä§Ìä∏ (send Ï†ÑÎã¨ Ïö©)
      chatterTotalList: [],  // Ï±ÑÌåÖÏûê Ï†ÑÏ≤¥ Î¶¨Ïä§Ìä∏
      newMessage: '',
      lastReadIndex: -1, // Ï¥àÍ∏∞ÏóêÎäî ÎßàÏßÄÎßâÏúºÎ°ú ÏùΩÏùÄ Ï±ÑÌåÖ ÏóÜÏùåÏùÑ ÎÇòÌÉÄÎÉÑ
      chatEnterFlag: false,
      userNo: '',
      userName: '',
      projectNo: '',
      websocket: '',
      isScrolledToBottom: true, // Ïä§ÌÅ¨Î°§Ïù¥ Ï†úÏùº ÏïÑÎûòÎ°ú Ïù¥ÎèôÌñàÎäîÏßÄ Ïó¨Î∂ÄÎ•º Ï∂îÏ†Å
      // address: "ws://localhost:8081/oqplanner/ws/chat", // ÏûÑÏãú
      address: 'ws://13.209.197.38:8081/oqplanner/ws/chat',
    };
  },
  methods: {
    connect(){
      this.websocket = new WebSocket(this.address);

      if(!this.chatEnterFlag){  // Ï≤´ ÏûÖÏû•
        this.websocket.onopen=()=>{
            console.log('Connection opened');
              this.websocket.send(JSON.stringify(this.chatMessages[0]));
              this.chatMessages = [];
        }
      }

      this.websocket.onmessage = (event)=>{
          console.log('take a message');

          try{
            const message = JSON.parse(event.data);

            // Î∞õÏùÄ Î©îÏãúÏßÄÎ•º chatTotalMessages Î∞∞Ïó¥Ïóê Ï∂îÍ∞ÄÌïòÏó¨ ÌôîÎ©¥Ïóê ÌëúÏãú
            const currentDate = new Date();
            const formattedDate = `${currentDate.getFullYear().toString().substr(-2)}-${(currentDate.getMonth() + 1).toString().padStart(2, '0')}-${currentDate.getDate().toString().padStart(2, '0')} ${currentDate.getHours().toString().padStart(2, '0')}:${currentDate.getMinutes().toString().padStart(2, '0')}:${currentDate.getSeconds().toString().padStart(2, '0')}`;
            this.chatTotalMessages.push({
              userNo: message.userNo,
              userName: message.userName,
              text: message.text,
              date: formattedDate,
              systemMessage: message.systemMessage,
            });

            // ÌòÑÏû¨ Ï±ÑÌåÖÏûê Î™©Î°ù get
            if(message.flag=="enter" || message.flag=="exit"){
              this.getChatters();
            }

            if(this.userNo == message.userNo){
              this.moveScroll(); 
            }
          }catch(error){
            console.error('JSON ÌååÏã± Ïò§Î•ò:', error);
          }  
      }

    },
    
    onClose() {
        const currentDate = new Date();
        const formattedDate = `${currentDate.getFullYear().toString().substr(-2)}-${(currentDate.getMonth() + 1).toString().padStart(2, '0')}-${currentDate.getDate().toString().padStart(2, '0')} ${currentDate.getHours().toString().padStart(2, '0')}:${currentDate.getMinutes().toString().padStart(2, '0')}:${currentDate.getSeconds().toString().padStart(2, '0')}`;
        this.chatMessages.push({
            flag: "exit",
            userNo: this.userNo,
            userName: this.userName,
            text: ` [ÏïåÎ¶º] "${this.userName}"ÎãòÏù¥ Ìá¥Ïû• ÌïòÏÖ®ÏäµÎãàÎã§.`,
            date: formattedDate,
            systemMessage: true,
        });
        
        this.websocket.send(JSON.stringify(this.chatMessages[0]));
        this.websocket.close();
        this.chatMessages=[];
    },

    getChatters(){
      this.chatterTotalList=[]; // Ï¥àÍ∏∞Ìôî
      this.$axios.get('/trip/chat/chatter')
      .then(response => {
          response.data.forEach(item => {
              console.log(item);
              this.chatterTotalList.push(item);
          });
      }).finally(()=>{
        // this.moveScroll();
      });
    },

    getBeforMessages(){
      this.previousChatMessages=[];  // Ï¥àÍ∏∞Ìôî
      const value = {
            date: "",   // date ÏûÖÎ†•Ïãú Ìï¥Îãπ ÎÇ†ÏßúÏóê ÎåÄÌïú keyÍ∞íÏùÑ Í∞ÄÏ†∏Ïò§ÎèÑÎ°ù Ìï® (2024 *) ÌòïÏãùÏúºÎ°ú Í∞ÄÏ†∏Ïò§Í∏∞Ïóê
                        // date yymmddhhmmss Îã§ ÏïàÎÑ£Ïñ¥ÎèÑ ÏÉÅÍ¥ÄÏóÜÏùå
        };
      this.$axios.get('/trip/chat/message' ,{
          params: value,
      }).then(response => {

          response.data.forEach(item => {
              this.previousChatMessages.push(item);
          });
      }).finally(()=>{
        this.moveScroll();
      });
    },

    getTripUserInfo(){
        console.log("getTripUserInfo");
        const tripUser = {
            tripUserNo: this.userNo,
        };
        this.$axios.get('/trip/user/info', {
          params:tripUser
        }).then(userResponse => {
          this.userName = userResponse.data.tripUserName;
        });
         
    },
    enterChat(event){
      console.log("enterChat!!")
      event.stopPropagation();

      this.showChat = !this.showChat;
      this.showChatterList = this.showChat;
      this.showChatter = this.showChat;

      if(!this.chatEnterFlag){  // Ï≤´ÏûÖÏû•
          this.chatRoomText = "Ï±ÑÌåÖÎ∞©";

    
          (async ()=> {
             // Í∏∞Ï°¥ Ï±ÑÌåÖ ÎÇ¥Ïó≠ get
            await this.getBeforMessages();

            // ÌòÑÏû¨ ÏûÖÏû•Ïùò ÏïåÎ¶º push
            const currentDate = new Date();
            const formattedDate = `${currentDate.getFullYear().toString().substr(-2)}-${(currentDate.getMonth() + 1).toString().padStart(2, '0')}-${currentDate.getDate().toString().padStart(2, '0')} ${currentDate.getHours().toString().padStart(2, '0')}:${currentDate.getMinutes().toString().padStart(2, '0')}:${currentDate.getSeconds().toString().padStart(2, '0')}`;
            console.log("formattedDate ÏûÖÏû• date:: " + formattedDate);
           
            this.chatMessages.push({   // ÏûêÏã†Í≥º ÌÉÄÏù∏Ïùò ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏Ïóê Î≥¥Ïó¨ÏßÄÎäî chatTotalMessagesÏóê ÏûÖÏû• Ï∂îÍ∞Ä  
                                      // Ïù¥ÌõÑ webSocket.sendÎ°ú Ï†ÑÎã¨ÎêòÏñ¥ Îã§Î•∏ ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ÏóêÍ≤å Î≥¥Ïó¨Ïßê
              flag: "enter",
              userNo: this.userNo,
              userName: this.userName,
              text: ` [ÏïåÎ¶º] "${this.userName}"ÎãòÏù¥ ÏûÖÏû• ÌïòÏÖ®ÏäµÎãàÎã§.`,
              // date: new Date().toString(),
              date: formattedDate,
              systemMessage: true,
            });



          })();
          this.connect();  
      }

      if (this.showChat) {
          // Ï±ÑÌåÖÏ∞ΩÏù¥ Ïó¥Î¶¥ ÎïåÎßàÎã§ ÎßàÏßÄÎßâÏúºÎ°ú ÏùΩÏùÄ Ï±ÑÌåÖ ÏúÑÏπòÎ°ú Ïä§ÌÅ¨Î°§ Ïù¥Îèô
          console.log("showChat!!");
          this.moveScroll();
      }

      this.chatEnterFlag = true;
    },
    exitChat(event){
      console.log("exitChat!!")
      event.stopPropagation();
      this.onClose();
      this.chatEnterFlag = false;
      this.chatRoomText = "Ï±ÑÌåÖÎ∞© ÏûÖÏû•";  // Ï±ÑÌåÖÎ∞© Î∞ñÏùò Î¨∏Íµ¨ Î≥ÄÍ≤ΩÌïòÏó¨ ÏûÖÏû•ÎèÑÎ°ù Ïú†ÎèÑ
      this.showChat = !this.showChat;
      this.showChatterList = this.showChat;
      this.showChatter = this.showChat;
      this.chatMessages = [];
      this.chatTotalMessages=[];
    },
    toggleChatList(event){
      event.stopPropagation();
      this.showChatterList = !this.showChatterList;
      // if (this.showChatterList) {
      //     // Ï±ÑÌåÖÏ∞ΩÏù¥ Ïó¥Î¶¥ ÎïåÎßàÎã§ ÎßàÏßÄÎßâÏúºÎ°ú ÏùΩÏùÄ Ï±ÑÌåÖ ÏúÑÏπòÎ°ú Ïä§ÌÅ¨Î°§ Ïù¥Îèô
      //     // this.moveScroll();
      // }
    },

    toggleChat(event) {
      console.log("toggleChat chatEnterFlag :: " + this.chatEnterFlag);
      event.stopPropagation();
      this.showChat = !this.showChat;
      this.showChatterList = this.showChat;
      this.showChatter = this.showChat;
      
      if (this.showChat) {
          // Ï±ÑÌåÖÏ∞ΩÏù¥ Ïó¥Î¶¥ ÎïåÎßàÎã§ ÎßàÏßÄÎßâÏúºÎ°ú ÏùΩÏùÄ Ï±ÑÌåÖ ÏúÑÏπòÎ°ú Ïä§ÌÅ¨Î°§ Ïù¥Îèô
          this.moveScroll();
      }
    },
    sendMessage() {
      if (this.newMessage.trim() !== '') {
        const currentDate = new Date();
        const formattedDate = `${currentDate.getFullYear().toString().substr(-2)}-${(currentDate.getMonth() + 1).toString().padStart(2, '0')}-${currentDate.getDate().toString().padStart(2, '0')} ${currentDate.getHours().toString().padStart(2, '0')}:${currentDate.getMinutes().toString().padStart(2, '0')}:${currentDate.getSeconds().toString().padStart(2, '0')}`;
        this.chatMessages.push({
          flag: "message",
          userNo: this.userNo,
          userName: this.userName,
          date: formattedDate, 
          text: this.newMessage,
          systemMessage: false,
          });
        this.newMessage = '';
        this.websocket.send(JSON.stringify(this.chatMessages[0]));
        this.chatMessages = [];
      }
      // this.moveScroll(); // Ïä§ÌÅ¨Î°§ÌÅ¥Î¶≠ÏãúÏïÑÎûòÎ°ú Ï£ºÏÑù
     
    },
    moveScroll() {
      // Ïä§ÌÅ¨Î°§ÏùÑ Îß® ÏïÑÎûòÎ°ú Ïù¥Îèô
      this.$nextTick(() => {
        let chatWindow = this.$el.querySelector('.chat-window');
        if (chatWindow) {
          chatWindow.scrollTop = chatWindow.scrollHeight-50;
          this.isScrolledToBottom = true;
        }
      });
    },
    scrollToBottom() {
      this.moveScroll(); // Ïä§ÌÅ¨Î°§Ïù¥ Ï†úÏùº ÏïÑÎûòÎ°ú Ïù¥Îèô
      this.isScrolledToBottom = true; // Ïä§ÌÅ¨Î°§Ïù¥ Ï†úÏùº ÏïÑÎûòÎ°ú Ïù¥ÎèôÌñàÏùåÏùÑ ÌëúÏãú
    },
    handleScroll(event) {
      // Ïä§ÌÅ¨Î°§Ïù¥ ÏúÑÎ°ú Ïò¨ÎùºÍ∞îÎäîÏßÄ Ïó¨Î∂Ä ÌôïÏù∏
      if (event.deltaY < 0 || event.deltaY > 0) {
        this.isScrolledToBottom = false; // Ïä§ÌÅ¨Î°§Ïù¥ ÏúÑÎ°ú Ïò¨ÎùºÍ∞îÏùÑ Îïå isScrolledToBottomÏùÑ falseÎ°ú ÏÑ§Ï†ï
      }
    },
  },
  beforeUnmount() {
    window.removeEventListener('wheel', this.handleScroll);
  },

};
</script>

<style scoped>
