  position: fixed;
  bottom: 60px;
  right: 5px;
  z-index: 9999; /* Îã§Î•∏ ÏöîÏÜåÎ≥¥Îã§ ÏúÑÏóê ÏúÑÏπòÌïòÎèÑÎ°ù ÎÜíÏùÄ Í∞íÏúºÎ°ú ÏÑ§Ï†ï */
}

.chat-toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  background-color: #333;
  color: white;
  padding: 7px;
  border-radius: 5px;
  border: 1px solid gray;
}

.toggle-text {
  flex: 1;
}

.exit-button {
  background-color: #333;
  color: white;
  border: none;
  padding: 1px;
