  background-color: gray;
}

.chat-window {
  background-color: white;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
  max-height: 500px; /* ÎÜíÏù¥Î•º ÏµúÎåÄ ÎÜíÏù¥Î°ú Î≥ÄÍ≤Ω */
  overflow-y: auto;
  width: 500px; /* Ï±ÑÌåÖÏ∞ΩÏùò ÎÑàÎπÑÎ•º Í≥†Ï†ïÏãúÌÇµÎãàÎã§ */
  display: flex;
  flex-direction: column; /* ÏöîÏÜåÎì§ÏùÑ ÏÑ∏Î°ú Î∞©Ìñ•ÏúºÎ°ú Ï†ïÎ†¨Ìï©ÎãàÎã§. */
}


.chat-window ul {
  list-style-type: none;
  padding: 0;
  margin: 0; /* ÎÇ¥Î∂Ä Í∞ÑÍ≤©ÏùÑ ÏóÜÏï±ÎãàÎã§ */
}

.input-container {
  display: flex;
  align-items: center;
  border-top: 1px solid #ccc;
  padding-top: 10px;
}

input[type="text"] {
  flex: 1;
  padding: 5px;
  border-radius: 3px;
}

button {
  background-color: #333;
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 3px;
  cursor: pointer;
  margin-left: 10px;
}


/* Ï±ÑÌåÖÏûê Î™©Î°ùÏùò  Ïä§ÌÉÄÏùº */

.chat-list-window {
  background-color: #333;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
  max-height: 200px; /* ÎÜíÏù¥Î•º ÏµúÎåÄ ÎÜíÏù¥Î°ú Î≥ÄÍ≤Ω */
  overflow-y: auto;
  width: 500px; /* Ï±ÑÌåÖÏ∞ΩÏùò ÎÑàÎπÑÎ•º Í≥†Ï†ïÏãúÌÇµÎãàÎã§ */
  display: flex;
  flex-direction: column; /* ÏöîÏÜåÎì§ÏùÑ ÏÑ∏Î°ú Î∞©Ìñ•ÏúºÎ°ú Ï†ïÎ†¨Ìï©ÎãàÎã§. */
}


.chat-list-window ul {
  list-style-type: none;
  padding: 0;
}

.chat-list-window ul li {
  border-bottom: 2px solid white;
  border-top: 2px solid white;
  margin-top: -2px; /* ÏàòÏ†ïÎêú Î∂ÄÎ∂Ñ */
  padding: 10px 0;
  color: white;
  font-size: 12px;
}

.chat-list-window ul li span.date {
  font-size: 9px;
}

.chat-list-window ul li .emoticon {
  font-size: 12px;
  margin-right: 5px;
}



/* Ï±ÑÌåÖÏ∞Ω ÎÇ¥Î∂ÄÏùò Ï±ÑÌåÖ Ïä§ÌÉÄÏùº */

.chat-window li {
  display: flex;
  align-items: center;
  text-align: left;
  margin-bottom: 10px;
}

.chat-window li span {
  font-size: smaller;
}

.chat-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* ÏöîÏÜåÎì§ÏùÑ ÏÑ∏Î°ú Î∞©Ìñ•ÏúºÎ°ú ÏãúÏûëÏ†êÏóê Ï†ïÎ†¨Ìï©ÎãàÎã§. */
}

.user-chat {
  align-self: flex-end;
  justify-content: flex-end;
  background-color: #E5E5EA; /* ÏÇ¨Ïö©Ïûê Ï±ÑÌåÖ Ï∞Ω Î∞∞Í≤ΩÏÉâ */
}

.message-content {
  word-break: break-all;
}

.other-chat {
  align-self: flex-start;
  justify-content: flex-start;
  background-color: #E5E5EA; /* ÏÉÅÎåÄÎ∞© Ï±ÑÌåÖ Ï∞Ω Î∞∞Í≤ΩÏÉâ */
}

.system-chat {
  align-self: center !important;
  justify-content: center !important;
  /* background-color: transparent !important; Î∞∞Í≤ΩÏÉâÏùÑ Ìà¨Î™ÖÏúºÎ°ú ÏÑ§Ï†ï */
  background-color: #333 !important;
  color: white !important;
}

.chat {
  max-width: 80%;
  /* margin-bottom: 10px; */
  /* padding: 10px; */
  border-radius: 15px; /* Îë•Í∑º Î™®ÏÑúÎ¶¨ */
}

.other-person-name {
  margin-bottom: 5px; /* Ïù¥Î¶ÑÍ≥º ÌÖçÏä§Ìä∏ ÏÇ¨Ïù¥Ïùò Í∞ÑÍ≤© ÏÑ§Ï†ï */
  font-weight: bold; /* Ïù¥Î¶ÑÏùò Í∏ÄÍº¥ÏùÑ ÎëêÍ∫ºÏö¥Ï≤¥Î°ú ÏÑ§Ï†ï */
  color: #333;
  font-size: 15px !important;
}

.chat-date {
  font-size: 10px !important;
}

.scroll-to-bottom {
  position: absolute; /* Ï†àÎåÄ ÏúÑÏπò ÏßÄÏ†ï */
  bottom: 25px; /* ÌïòÎã®ÏúºÎ°úÎ∂ÄÌÑ∞Ïùò Í±∞Î¶¨ ÏÑ§Ï†ï */
  left: 50%; /* Í∞ÄÎ°ú Ï§ëÏïôÏúºÎ°ú Ïù¥Îèô */
  transform: translateX(-50%); /* Ï§ëÏïô Ï†ïÎ†¨ÏùÑ ÏúÑÌïú Î≥ÄÌòï(transform) ÏÑ§Ï†ï */
  background-color: #333; /* Î∞∞Í≤ΩÏÉâ ÏßÄÏ†ï */
  color: white; /* Í∏ÄÏûêÏÉâ ÏßÄÏ†ï */
  padding: 10px; /* ÎÇ¥Î∂Ä Ïó¨Î∞± ÏÑ§Ï†ï */
  border-radius: 5px; /* Î™®ÏÑúÎ¶¨ Îë•Í∏ÄÍ≤å ÏÑ§Ï†ï */
  cursor: pointer; /* ÎßàÏö∞Ïä§ Ïª§ÏÑúÎ•º Ìè¨Ïù∏ÌÑ∞Î°ú Î≥ÄÍ≤ΩÌïòÏó¨ ÌÅ¥Î¶≠ Í∞ÄÎä•Ìï®ÏùÑ ÎÇòÌÉÄÎÉÑ */
}

.scroll-to-bottom:hover {
  background-color: #555; /* ÎßàÏö∞Ïä§Î•º Ïò¨Î†∏ÏùÑ ÎïåÏùò Î∞∞Í≤ΩÏÉâ ÏßÄÏ†ï */
}





</style>
